@import '../../styles/_variables';
@import '../../styles/__fonts';

header {
    align-items: center;
    display: flex;
    height: 96px;
    justify-content: space-between;
    margin: 40px 0 0 55px;
    font-family: $head-5-font;
    color: $color-white;

    .logo {
        margin-right: 20px; }

    img {
        height: 48px;
        object-fit: contain; }

    ul {
        list-style: none;
        padding: 0 10%;
        display: flex;
        justify-content: space-between;
        background: #ffffff0a;
        backdrop-filter: blur(4px);
        height: 100%; }




    li {
        @extend %subhead-3;
        cursor: pointer;
        padding: 0 20px; }


    .unactive:hover {
        border-bottom: 3px solid #ffffff80;
        border-top: 3px solid rgba(0, 0, 0, 0);
        height: 90px; }

    .active {
        border-bottom: 3px solid $color-white;
        border-top: 3px solid rgba(0, 0, 0, 0);
        height: 90px; }

    .unactive {
        height: 96px; }

    span {
        @extend %subhead-3;
        font-weight: 700;
        margin: 0 11px; }

    a {
        text-decoration: none;
        color: $color-white;
        display: flex;
        align-items: center; }

    hr {
        border: none;
        background: #97979748;
        height: 1px;
        width: 40%;
        z-index: 1;
        transform: translateX(20%); } }

@media (max-width: 820px) {

    header {

        ul {

            padding-left: 5%;
            padding-right: 50px;
            width: 65%;

            li {
                padding-left: 1%;
                padding-right: 1%; }

            span {
                display: none; }

            a {
                margin: 0 5px; } }

        hr {
            display: none; } } }

