@import '../../styles/_variables';
@import '../../styles/__fonts';

.container-technology {
    display: grid;
    grid-template-columns: 1fr 4fr 5fr;
    grid-auto-rows: 1fr 1fr 1fr 250px;
    position: absolute;
    bottom: 10%;
    left: 10%;
    top: 150px;
    color: $color-white;
    margin: 0;


    .title {
        @extend %head-5;
        grid-column: 1/4;
        grid-row: 1/2;

        span {
            opacity: 25%; } }

    .subtitle {
        grid-column: 2/3;
        grid-row: 2/3;
        @extend %subhead-3;
        color: $color-silver;
        margin-top: 100px;
        margin-bottom: 0px; }

    .image {
        grid-column: 3/4;
        grid-row: 1/6;
        justify-self: right;
        align-self: center;

        img {
            max-width: 100%; } }

    .type {
        grid-column: 2/3;
        grid-row: 3/4;

        h3 {
            @extend %head-3;
            font-weight: 400;
            margin: 0; } }

    .description {
        grid-column: 2/3;
        grid-row: 4/5;
        @extend %bodytext;
        color: $color-silver;
        padding-right: 15%;
        p {
            margin-bottom: 0; } }

    .navigate-technology {
        grid-column: 1/2;
        grid-row: 2/5;

        .navigate-dot-list {
            display: flex;
            flex-direction: column;
            padding: 0;
            margin: 80px 0 0 0;
            height: 390px;
            justify-content: space-between;

            input {
                height: 80px;
                width: 80px;
                margin: 0;
                border-radius: 100%;
                @extend %head-4; }

            .active-dot {
                background: $color-white;
                color: $color-black;
                border: none; }

            .unactive-dot {
                background: #ffffff00;
                border: 1px solid #ffffff57; }

            .unactive-dot:hover {
                border-color: $color-white; } } } }

@media (max-width: 820px) {

    .container-technology {
        display: grid;
        color: $color-white;
        grid-template-columns: 1fr;
        grid-auto-rows: 1fr 300px 170px 50px 1fr 1fr;
        height: 70%;
        text-align: center;
        top: 130px;
        margin: 0;
        left: 0;
        .title {
            grid-column: 1/2;
            grid-row: 1/2;
            text-align: left;
            margin-top: 11px;
            margin-left: 50px; }
        .image {
            grid-column: 1/2;
            grid-row: 2/3;
            width: 100%;
            margin: 0 auto;
            img {
                height: 330px;
                width: 100%;
                object-fit: cover;
                object-position: 0% 85%; } }
        .navigate-technology {
            grid-column: 1/2;
            grid-row: 3/4;
            margin: 0 auto 0;
            width: 100%;

            .navigate-dot-list {
                flex-direction: row;
                margin: 0 auto;

                height: 0;
                justify-content: center;
                input {
                    margin: 50px 20px 0;
                    height: 65px;
                    width: 65px;
                    font-size: 24px; } } }


        .type {
            grid-column: 1/2;
            grid-row: 5/6;
            h3 {
                font-size: 40px;
                line-height: 32px;
                letter-spacing: 2.75px; } }
        .subtitle {
            grid-column: 1/2;
            grid-row: 4/5;
            margin: 0;
            h5 {
                margin: 0;
                font-size: 16px;
                opacity: 50%; } }
        .description {
            grid-column: 1/2;
            grid-row: 6/7;
            padding: 0 10%;
            p {
                font-size: 16px; } } } }

@media (max-width: 600px) {

    .container-technology {
        grid-auto-rows: 1fr 230px 150px 50px 1fr 1fr;

        .title {
            text-align: center;
            margin-left: 0; }
        .image {
            img {
                height: 220px; } }
        .type {
            h3 {
                font-size: 24px; } }
        .description {
            p {
                font-size: 15px; } }
        .navigate-technology {
            .navigate-dot-list {
                input {
                    height: 50px;
                    width: 50px;
                    font-size: 16px; } } } } }
