@import '../../styles/_variables';
@import '../../styles/__fonts';


.main-page {
    display: grid;
    grid-template-columns: 440px 1fr 274px;
    grid-template-rows: 500px;
    margin: 151px 0 131px 8%;
    column-gap: 40px;

    .main-page-description {
        color: $color-white;
        grid-column: 1/2;

        h5 {
            @extend %head-5;
            margin: 0 0 24px; }

        h1 {
            @extend %head-1; }

        p {
            @extend %bodytext;
            height: 130px; } }


    img {
        width: 274px;
        height: 274px;
        border-radius: 100%;
        transform: translate(-60px, -100px); }


    img:hover {
        box-shadow: 0 0 0 60px rgba(94, 94, 94, 0.5);
        transition: 0.15s; }

    a {
        align-self: flex-end;
        justify-self: flex-end;
        grid-column: 3/4; } }


@media (max-width: 820px) {

    .main-page {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
        margin: 60px 10%;
        padding-bottom: 60px;
        text-align: center;

        p {
            padding: 0 10%; }


        a {
            grid-column: 1/2;
            margin: 60px auto 0; }

        img {
            transform: translate(0px, 0px); } } }

@media (max-width: 600px) {

    .main-page {
        margin: 50px 0 0;
        img {
            width: 70%;
            height: 70%;
            margin-top: 40px; }
        .main-page-description {
            h1 {
                font-size: 80px; }

            h5 {
                font-size: 16px; }
            p {
                padding: 0 10%;
                font-size: 15px; } } } }
